import httpClient from '../utils/axios';
import {
	LOGOUT,
	SET_USER,
	START_LOGIN,
	SET_ERROR_LOGIN,
	SET_SUCCESS_LOGIN,
	RESTART_ERROR_SET_USER,
} from '../constants';

const dummyUserApi = {
	id: 1,
	password: '123',
	name: 'Demo User',
	email: 'example@gmail.com',
};

let dummyUser = null;

/*
 * @param {Object} payload | User data 
 * @param {Function} dispath
*/
export const setUser = async ({ email, password }, dispath) => {
	dispath({
		type: START_LOGIN,
		payload: {
			isLoginLoading: true
		}
	})
	
	setTimeout(() => {
		dispath({
			type: START_LOGIN,
			payload: {
				isLoginLoading: false
			}
		})
		
		if(false) {
			dispath({
				type: SET_ERROR_LOGIN,
				payload: {
					errorMsj: 'data.message'
				}
			})
		} else {
			dummyUser = {...dummyUserApi};
			dispath({
				type: SET_SUCCESS_LOGIN,
				payload: {
					user: dummyUserApi,
					errorMsj: null,
					isLogin: true
				}
			})
		}
	}, 2000);
}

export const verifySession = (dispath) => {
	return new Promise((resolve) => {
		setTimeout(() => {
			if(dummyUser) {
				dispath({
					type: SET_USER,
					payload: {
						user: dummyUser,
						isLogin: true
					}
				})
			}
			resolve();
		}, 2000)
	})
}

export const restartErrorLogin = () => {
	return {
		type: RESTART_ERROR_SET_USER,
		payload: {
			errorMsj: null
		}
	}
}

export const logout =  () => {
	dummyUser = null;
	return {
		type: LOGOUT
	}
}